import {Component} from '@angular/core';
import {AlertController, IonicPage, NavController, NavParams} from 'ionic-angular';
import {TokenServiceProvider} from "../../providers/token-service/token-service";
import {LocalStorageProvider} from "../../providers/local-storage/local-storage";
import {MaskTokenProvider} from "../../providers/mask-token/mask-token";
import {CryptoParamUtilProvider} from "../../providers/crypto-param-util/crypto-param-util";
import {GoogleAnalyticsProvider} from "../../providers/google-analytics/google-analytics";
import {FundTransferOtherBankProvider} from "../../providers/fund-transfer-other-bank/fund-transfer-other-bank";
import {ConfigProvider} from "../../providers/config/config";


@IonicPage()
@Component({
  selector: 'page-delete-credit-account-other-bank01',
  templateUrl: 'delete-credit-account-other-bank01.html',
})
export class DeleteCreditAccountOtherBank01Page {

  deleteCreditAccountOBError01 = "";
  accountNo = "";
  listSize = 0;

  fundTransferType;
  receiverType;
  creditAccList;
  routeShow;
  beneficiaryId;

  constructor(public navCtrl: NavController,
              public navParams: NavParams,
              public tokenService: TokenServiceProvider,
              public storage: LocalStorageProvider,
              public maskTokenUtil: MaskTokenProvider,
              public cryptoUtil: CryptoParamUtilProvider,
              public iTransferService: FundTransferOtherBankProvider,
              public alertCtrl: AlertController,
              public config: ConfigProvider,
              public gaProvider: GoogleAnalyticsProvider) {

    this.fundTransferType = this.navParams.get("fundTransferType");
    this.receiverType = this.navParams.get("receiverType");
    this.routeShow = this.fundTransferType != this.config.transferTypeNPSB;
    this.initCreditAccount();
  }

  initCreditAccount = function () {
    let appData = this.storage.retrieveFromStorage("appData");
    let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
    this.tokenService.getNewToken(unMaskRefToken)
      .then(data => {
        this.onNewTokenGenComplete(data);
      });

  };

  onNewTokenGenComplete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encTxService = this.cryptoUtil.getEncryptVal(this.fundTransferType);
      let encRec = this.cryptoUtil.getEncryptVal(this.receiverType);
      this.iTransferService.getObFtBeneficiaryAccountList(unMaskToken, appData.userId, appData.appId, encTxService, encRec)
        .then(data => {
          this.onFetchOBCreditAccountListComplete(data);
        });
    }
  };

  onFetchOBCreditAccountListComplete = function (data) {
    if (data.statusCode == 1) {
      this.creditAccList = data.object.obFtBeneficiaries;
      if (this.creditAccList == null) {
        this.listSize = 0;
        this.deleteCreditAccountOBError01="You have no credit account subscription. Please subscribe first."
      }
      else {
        this.listSize = this.creditAccList.length;
      }

    }

    else if (data.statusCode == 0) {
      this.deleteCreditAccountOBError01 = data.note;
    }

    else {
      this.deleteCreditAccountOBError01 = "System Error Contact with Administrator.";
    }
  };


  deleteOtherBankCreditAccount = function (listItem) {
    this.beneficiaryId = listItem.beneficiaryId;
    this.deleteCreditAccountOBError01 = "";

    let radioAlert = this.alertCtrl.create();
    radioAlert.setTitle('Confirm Delete');
    radioAlert.setCssClass("alertLogCss");
    radioAlert.setMessage("<center>Are you sure you want to delete Account : <strong>" + listItem.beneficiaryAccount + "</strong>" + " Beneficiary Name : <strong>"+listItem.beneficiaryName+"</strong></center>");
    radioAlert.addButton('Cancel');
    radioAlert.addButton({
      text: 'OK',
      handler: data => {
        let appData = this.storage.retrieveFromStorage("appData");
        let unMaskRefToken = this.maskTokenUtil.getUnMaskVal(appData.refreshToken);
        this.tokenService.getNewToken(unMaskRefToken)
          .then(data => {
            this.onNewTokenGenCompleteForDelete(data);
          });
      }
    });
    radioAlert.present();
    setTimeout(() => {
      radioAlert.dismiss();
    }, 20000);
  };

  onNewTokenGenCompleteForDelete = function (data) {

    if (data.status == 0) {
      this.navCtrl.setRoot('ExpirePage');
    }

    else {
      let appData = this.storage.retrieveFromStorage("appData");
      let unMaskToken = this.maskTokenUtil.getUnMaskVal(appData.token);
      let encBenId = this.cryptoUtil.getEncryptVal(this.beneficiaryId);
      let encTxService = this.cryptoUtil.getEncryptVal(this.fundTransferType);
      let encRec = this.cryptoUtil.getEncryptVal(this.receiverType);
      this.iTransferService.removeObFtBeneficiaryAccount(unMaskToken, appData.userId, appData.appId, encBenId, encTxService, encRec)
        .then(data => {
          this.onDeleteOBCreditAccountListComplete(data);
        });
    }
  };

  onDeleteOBCreditAccountListComplete = function (data) {
    if (data.statusCode == 1) {
      let radioAlert = this.alertCtrl.create();
      radioAlert.setTitle('Success');
      radioAlert.setCssClass("alertLogCss");
      radioAlert.setMessage("<center>Credit Account successfully Deleted.</center>");
      radioAlert.addButton({
        text: 'OK',
        handler: data => {
          this.initCreditAccount();
        }
      });
      radioAlert.present();
      setTimeout(() => {
        radioAlert.dismiss();
      }, 20000);

    }

    else if (data.statusCode == 0) {
      this.deleteCreditAccountOBError01 = data.note;
    }

    else {
      this.deleteCreditAccountOBError01 = "System Error Contact with Administrator.";
    }
  };

  ionViewDidLoad() {
    this.gaProvider.startTrackerWithId("Manage Other Bank Credit Account");
  }


}



